html{
  zoom: 100%;
  scroll-behavior: smooth;
  height: 100%;
}
body{
padding: 0px;
}

table{
  background-color: paleturquoise;
  border: solid 2px black;
  border-radius: 5px;
  width: 100%;
  height: 80%;

}

a{
 text-decoration: none;
  color: black;

}
.time{

text-align: center;

}
.table-comp{
  font-family: "Corbel Light";
  font-size: large;

}

a:hover{
background-color: black;
color: white;}



.table-head{

  font-size: xx-large;
  background-color: seashell;
  width: 30%;
  height: 10%;
  text-align: center;
  font-family: "Lucida Sans Unicode";
}

#title{
  margin-left: 1px;
  font-size: xx-large;
  font-family: "JetBrains Mono";
  border: solid 2px black;

}
#title:hover{

  background-color: black;
  color: white;

}


.nav-field{

  margin-left: 2%;
  alignment: center;
  justify-content: center;

}
.table-comp{
  border: 2px solid black;
  width: 30%;



}
td{

  width: 30%;
}



.Head{

height: 2%;

}
footer{

  height: 20%;
}

.class1{

  background-color: lightblue;


}
.class3{


  color: green;

}


li{

  list-style-type: none;
  display: inline;


}
footer > p{

  font-size: xx-large;
  font-family: "Lucida Console";
  margin-top: 5%;

}


